<template>
    <div>
        <div v-if="ready">

            <div v-if="sidebar" class="animate__animated animate__fadeInLeft" style="position:fixed;z-index:100000;top:0;left:0;width:100vw;height:100vh;">
                <div style="height:100vh;width:100vw;" class="bg-green-600 pl-8 pr-8">
                    <span class="typcn typcn-th-list text-4xl  text-white float-right cursor-pointer" @click="sidebar=false"></span>
                    <div style="min-height:100px;">
                    </div>
                    <div class="row ">
                    <div class="col-10" v-if="$store.state.users">
                            <div @click="changePage(item.link)" class="
                            cursor-pointer
                            border-b-2 my-2 border-green-300 text-white font-bold text-sm" v-for="(item, index) in listMenu" :key="index+'listMenu'" :to="item.link"
                            v-if="item.roles.length>0?item.roles.includes($store.state.users.role):true"
                            >
                                <span class="typcn text-2xl text-white" :class="`typcn-${item.icon}`"></span> {{item.title}}
                            </div>
                    </div>
                    </div>
                    <p class="text-white font-bold text-lg absolute cursor-pointer" 
                    style="bottom:30px;left:30px;"
                    @click="logout"
                    >Logout <span class="typcn  ml-2 typcn-arrow-right"></span></p>
                </div>
            </div>
            <div class="p-8 d-block d-sm-none">
                <span class="typcn typcn-th-list text-4xl cursor-pointer" @click="sidebar=!sidebar"></span>
                <span @click="logout" class="typcn typcn-user cursor-pointer text-4xl float-right text-red-400" ></span>
            </div>
            <div class="pt-1 pl-6 pr-6 d-none d-sm-block">
                <span class="typcn typcn-th-list text-3xl cursor-pointer" @click="sidebar=!sidebar"></span>
                <span @click="logout" class="typcn typcn-user cursor-pointer text-3xl float-right text-red-400" ></span>
            </div>
            <hr class="mb-4">
            <router-view></router-view>
        </div>
        <div v-else>
            <div style="height:100vh;" class="d-flex justify-content-center align-items-center shadow-lg rounded-lg">
                <div style="width:100%">
                    <p class="text-center animate__animated animate__bounce animate__infinite">Loading ...</p>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import firebase from 'firebase'
let db = firebase.firestore();
export default{
    data(){
        return{
            ready:false,
            sidebar:false,
            listMenu:[
                {
                    title:'Report',
                    link:'/hit',
                    icon:'home',
                    roles:[]
                },
                {
                    title:'customer',
                    link:'/hit/customer',
                    icon:'cog',
                    roles:[]
                },
                {
                    title:'assigned',
                    link:'/hit/assigned',
                    icon:'cog',
                    roles:[]
                },
                {
                    title:'tags',
                    link:'/hit/tags',
                    icon:'cog',
                    roles:[]
                },
                {
                    title:'stage',
                    link:'/hit/stage',
                    icon:'cog',
                    roles:[]
                },
                {
                    title:'helpdesk',
                    link:'/hit/helpdesk',
                    icon:'cog',
                    roles:[]
                },
                {
                    title:'ticket_type',
                    link:'/hit/ticket_type',
                    icon:'cog',
                    roles:[]
                },
            ]
        }
    },
    mounted() {
        this.ceklogin();
        let that=this;
        // document.addEventListener(
        // "deviceready",
        // function() {
        //     document.addEventListener("backbutton", function(e) {
        //         if(that.$route.name=='admin-pks'||that.$route.name=='auth-login'){
        //             e.preventDefault();
        //             navigator.app.exitApp();
        //             return;
        //         }
        // }, false);})
    },
    methods: {
        changePage(data){
            this.$router.push(data)
            this.sidebar=false
        },
        logout(){
            firebase.auth().signOut().then(() => {
                console.log('logout berhasil')
                }).catch((error) => {
            });
        },
        ceklogin(){
            firebase.auth().onAuthStateChanged((users) => {
                console.log('users',users)
                if (users) {
                    db.collection(`users`).get().then(res=>{
                        let data = res.docs.map(doc=>doc.data())
                        data=data.filter(e=>e.uid==users.uid)
                        // console.log('data usernya',data[0].role)
                        // if(data[0].aktif=='false' || !data[0].aktif){
                        //     this.$router.push('/error/akun-tidak-aktif')
                        // }
                        // if(!data[0].role || data[0].role=='false'){
                        //     this.$router.push('/error/akun-belum-ada-role')
                        // }
                        firebase.auth().currentUser.getIdToken(/* forceRefresh */ true).then(function(idToken) {
                            localStorage.setItem("auth._token.local", sdb.scramble('bearer '+idToken));
                            localStorage.setItem("auth.local", sdb.scramble('bearer '+idToken));
                            }).catch(function(error) {
                        });
                        // if(data[0].changepassword=='true'){
                        //     this.$router.push('/auth/changepassword');
                        // }
                        this.$store.commit("changeUsers", data[0]);
                        localStorage.setItem("users", JSON.stringify(data[0]));
                        this.ready=true;
                        this.$forceUpdate();
                    })
                } else {
                    console.log('user sesion tidak ada')
                    this.$router.push('/auth/login')
                }
            });
        }
    },
    
}
</script>